import React from "react";
import { Component } from 'react';
import { Responsive, WidthProvider } from "react-grid-layout";
import BarCharts from "../Graphs/BarChart";
import dataPerformance from "../Data/PerformanceData";
import Table from "../Graphs/Table";
import LineCharts from "../Graphs/LineChart";
import * as Sentry from "@sentry/react";

const ResponsiveGridLayout = WidthProvider(Responsive);

const dataPerformance_1 = dataPerformance.DataPerformance1;
const dataPerformance_2 = dataPerformance.DataPerformance2;
const dataPerformance_3 = dataPerformance.DataPerformance3;
const dataPerformance_4 = dataPerformance.DataPerformance4;
const dataPerformance_5 = dataPerformance.DataPerformance5;
const dataPerformance_6 = dataPerformance.DataPerformance6;
const chart_Color = "#1DDDF5b4";


export default class SecondTab extends Component {
    constructor() {
        super();
        this.state = {
          value: true,
        };
        this.onHandle = this.onHandle.bind(this);
      }
      onHandle() {
        this.setState((prevState) => ({
          value: !prevState.value,
        }));
      }
      render() {
        var date = Date().toLocaleString()
        Sentry.captureMessage(date + ";Klassifikatorgüte;userName 1001");
        var layout = [
          { i: "a", x: 0, y: 0, w: 1, h: 1},
          { i: "b", x: 1, y: 0, w: 1, h: 1},
          { i: "c", x: 0, y: 1, w: 1, h: 1},
          { i: "d", x: 1, y: 1, w: 1, h: 1},
        ];
    
        var layout = { lg: layout };
        
        return (
          <div>
            <ResponsiveGridLayout
              className="layout"
              layouts={layout}
              breakpoints={{ lg: 1200, md: 996, sm: 768, xs: 480, xxs: 0 }}
              cols={{ lg: 2, md: 2, sm: 1, xs: 1, xxs: 1 }}
              width={1200}
              rowHeight={350}
              isResizable
              isDraggable={false}
            >
              <div id="div1" key="a">
                <BarCharts title="Genauigkeit [Flow]" data={dataPerformance_1} dataKey="Genauigkeit" chartColor={chart_Color}/>
              </div>
              <div id="div1" key="b">
                <BarCharts title="F1-Score [Flow]" data={dataPerformance_2} dataKey="F1-Score" chartColor={chart_Color}/>
              </div>
              <div id="div1" key="c">
                <BarCharts title="Genauigkeit [Mentale Arbeitsbelastung]" data={dataPerformance_4} dataKey="Genauigkeit" chartColor={chart_Color}/>
              </div>
              <div id="div1" key="d">
                <BarCharts title="F1-Score [Mentale Arbeitsbelastung]" data={dataPerformance_5} dataKey="F1-Score" chartColor={chart_Color}/>
              </div>
            </ResponsiveGridLayout>
          </div>
        );
      }
    }