import React, { PureComponent } from 'react';
import { ResponsiveContainer, PieChart, Pie, Legend, Tooltip, Sector, Label, Cell } from 'recharts';

const COLORS = ['#0088FEc5', '#00C49Fc5', '#FFBB28c5', '#FF8042c5', '#0BC8DBc5'];

function  PieCharts(props){
    const title = props.title;
    const data = props.data;
    const dataKey = props.dataKey;
    const chartColor = props.chartColor;
    return (
        <ResponsiveContainer width="99%" height="99%" >
            <PieChart
            width={500}
            margin={{
            top: 80,
            right: 70,
            left: 30,
            bottom: 30,
            }}>
                <text x={20} y={30} fill="white" textAnchor="left" dominantBaseline="central">
                    <tspan fontSize="26" fontWeight={"lighter bold"}>{title}</tspan>
                </text>
                <Tooltip />
                <Legend layout='horizontal'/>
                <Pie dataKey= {dataKey} data={data} fill={chartColor} label >    
                    {data.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                </Pie>
            </PieChart>
        </ResponsiveContainer>
    )
}

export default PieCharts;