import React from "react";
import { Component } from 'react';
import { Responsive, WidthProvider } from "react-grid-layout";
import BarCharts from "../Graphs/BarChart";
import dataCognitive from "../Data/CognitiveActivityData";
import dataFlow from "../Data/Results_Flow_PerDay1001.json";
import dataWorkload from  "../Data/Results_Workload_PerDay1001.json";
import BarChartsTwo from "../Graphs/BarChart_two";
import LineCharts from "../Graphs/LineChart";
import * as Sentry from "@sentry/react";


const ResponsiveGridLayout = WidthProvider(Responsive);


const dataWorkload_4 = dataCognitive.HoursOfWorkload;
const dataWorkload_5 = dataCognitive.WorkLoadPerHour;
const dataWorkload_6 = dataCognitive.WorkloadTBD;

const chart_Color = "#27DFC0b4";
const chart_Color2= "#DE3136b4";

//throw new Error("ThirdTab.js userName 1001")
export default class ThirdTab extends Component {

    constructor() {
        super();
        this.state = {
          value: true,
        };
        this.onHandle = this.onHandle.bind(this);
      }
      onHandle() {
        this.setState((prevState) => ({
          value: !prevState.value,
        }));
      }
      
      render() {
        var date = Date().toLocaleString()
        Sentry.captureMessage(date + ";Kognitive Tätigkeiten; userName 1001");
        var layout = [
          { i: "a", x: 0, y: 0, w: 1, h: 1},
          { i: "b", x: 1, y: 0, w: 1, h: 1},
          { i: "d", x: 0, y: 1, w: 1, h: 1},
          { i: "e", x: 1, y: 1, w: 1, h: 1},
        ];
    
        var layout = { lg: layout };
        return (
          <div>
            <ResponsiveGridLayout
              className="layout"
              layouts={layout}
              breakpoints={{ lg: 1200, md: 996, sm: 768, xs: 480, xxs: 0 }}
              cols={{ lg: 2, md: 2, sm: 1, xs: 1, xxs: 1 }}
              width={1200}
              rowHeight={350}
              isResizable
              isDraggable={false}
            >
              <div id="divFlowTag" key="a">
                <BarChartsTwo title="Flow pro Tag [h]" data={dataFlow} dataKey1="flowMinutes" dataKey2="totalMin" chartColor1={chart_Color} chartColor2= {chart_Color2}/>
              </div>
              <div id="divFlowStunde" key="b">
                <BarCharts title="Flow pro Stunde [%]" data={dataFlow} dataKey="flowPercentage" yAxisMax = {100} chartColor={chart_Color}/>
              </div>
              <div id="divWorkloadTag" key="d">
                <BarChartsTwo title="Hohe mentale Arbeitsbelastung pro Tag [h]" data={dataWorkload} dataKey1="highWMMins" dataKey2="totalMin" chartColor1={chart_Color} chartColor2= {chart_Color2}/>
              </div>
              <div id="divWorkloadStunde" key="e">
                <BarCharts title="Hohe mentale Arbeitsbelastung pro Stunde [%]" data={dataWorkload} dataKey="highWMPercentage" yAxisMax = {100} chartColor={chart_Color}/>
              </div>
            </ResponsiveGridLayout>
          </div>
        );
      }
    }