import React, { Component } from 'react'
import ReactTable from "react-table";

function Table (props){

    const title = props.title;
    const data = { "postives": [

                    {
                        "rowname":"Monday",
                        "positives":200,
                        "negatives":100,
                    },
                    ],
                    "negatives": [

                        {
                            "rowname":"Monday",
                            "positives":200,
                            "negatives":100,
                        },
                    ], 
                };
    return(
        <div class="table-responsive" height="10vh">
            <text id="tile" x={20} y={20} fill="white">
                <tspan>Confusion Matrix</tspan>
            </text>
            <table class="table table-bordered table-dark">
                <thead>
                    <tr>
                    <th scope="col"></th>
                    <th scope="col">Richtig</th>
                    <th scope="col">Falsch</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                    <th scope="row">Richtig</th>
                    <td>86</td>
                    <td>12</td>
                    </tr>
                    <tr>
                    <th scope="row">Falsch</th>
                    <td>6</td>
                    <td>90</td>
                    </tr>
                </tbody>
            </table>
        </div>
    )
}

export  default Table;