import {
    BarChart,
    Bar,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ResponsiveContainer
  } from "recharts";

  

function  BarCharts(props){
    const title = props.title;
    const data = props.data;
    const dataKey = props.dataKey;
    const chartColor = props.chartColor;
    const yAxisMax = props.yAxisMax;
    return (
    <ResponsiveContainer width="99%" height="99%" >    
        <BarChart
          data={data}
          width={500}
          margin={{
            top: 100,
            right: 70,
            left: 30,
            bottom: 30,
          }
        }
        >
            <text x={20} y={30} fill="white" textAnchor="left" dominantBaseline="central">
                <tspan fontFamily="Segoe UI" fontSize="26"  fontWeight="regular">{title}</tspan>
            </text>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="Day" stroke="white"/>
            <YAxis stroke="grey" domain={[0, yAxisMax]}/>
            <Tooltip />
            <Bar dataKey= {dataKey} fill= {chartColor}/>
        </BarChart>
    </ResponsiveContainer>
    )
}

export  default BarCharts;