import React from "react";
import { Component } from 'react';
import { Responsive, WidthProvider } from "react-grid-layout";
import BarCharts from "../Graphs/BarChart";
import dataCollected1 from "../Data/CollectedData";
import dataFlow from "../Data/Results_Flow_PerDay1001.json";
import LineCharts from "../Graphs/LineChart";
import PieCharts from "../Graphs/PieChart";
import * as Sentry from "@sentry/react";

const ResponsiveGridLayout = WidthProvider(Responsive);

const dataCollected_1 = dataFlow;
const dataCollected_2 = dataCollected1.CollectedData2;
const dataCollected_3 = dataCollected1.CollectedData3;
const dataCollected_Tasks = dataCollected1.CollectedDataTasks;
const dataCollected_5 = dataCollected1.CollectedData5;
const dataCollected_6 = dataCollected1.CollectedData6;
const chart_Color = "#1C9EEBb4";

export default class FirstTab extends Component {
constructor() {
    super();
    this.state = {
      value: true,
    };
    this.onHandle = this.onHandle.bind(this);
  }
  onHandle() {
    this.setState((prevState) => ({
      value: !prevState.value,
    }));
  }
  render() {
    var date = Date().toLocaleString()
    Sentry.captureMessage(date + ";Übersicht;userName 1001");
    var layout = [
      { i: "a", x: 0, y: 0, w: 3, h: 1},
      { i: "b", x: 3, y: 0, w: 3, h: 1},
      { i: "c", x: 0, y: 0, w: 2, h: 1},
      { i: "d", x: 2, y: 1, w: 2, h: 1},
      { i: "e", x: 4, y: 1, w: 2, h: 1},

    ];

    var layout = { lg: layout };
    
    return (
      <div>
        
        <ResponsiveGridLayout
          className="layout"
          layouts={layout}
          breakpoints={{ lg: 1100, md: 996, sm: 768, xs: 480, xxs: 0 }}
          cols={{ lg: 6, md: 6, sm: 1, xs: 1, xxs: 1 }}
          width={1100}
          rowHeight={350}
          isResizable
          isDraggable={false}
        >
          <div id="div1" key="a" >
            <BarCharts title="Dauer Session [h]" data={dataFlow} dataKey="totalMin" chartColor= {chart_Color}/>
          </div>
          <div id="div1" key="b">
            <BarCharts title="Anzahl Sessions" data={dataCollected_2} dataKey="Sessions" chartColor={chart_Color}/>
          </div>
          <div id="div1" key="c">
            <BarCharts title="Gesammelte Daten [GB]" data={dataCollected_3} dataKey="Giga Bytes" chartColor={chart_Color}/>
          </div>
          <div id="div1" key="d">
            <BarCharts title="Beantwortete Fragebögen" data={dataCollected_5} dataKey="#Fragebögen" chartColor={chart_Color}/>
          </div>
          <div id="div1" key="e">
            <PieCharts title="Bearbeitete Aufgaben" data={dataCollected_Tasks} dataKey="Occurence" chartColor={chart_Color}/>
          </div>
        </ResponsiveGridLayout>
      </div>
    );
  }
}